import additives3 from "../../images/products/additives/3.jpg";
import additives4 from "../../images/products/additives/4.jpg";
import additives5 from "../../images/products/additives/5.jpg";

const products = [
  {
    products: "tshirts",
    img: [tshirt1, tshirt2, tshirt3, tshirt4, tshirt5],
    mainImg: mainTshirt
  }
];

const Container = styled.div`
  position: absolute;
  top: 60px;
`;

const Products = props => {
  if (props.match.params.products_id === "tshirts") {
    const newProducts = products
      .filter(product => product.products === "tshirts")[0]
      .img.map(im => <img key={im} src={im} alt="" />);

    console.log(newProducts);

    return <Container>{newProducts} t-shirty</Container>;
  } else if (props.match.params.products_id === "pants") {
    return (
      <Container>
        <h1>Sprzedajemy dzisiaj: {props.match.params.products_id}</h1>
      </Container>
    );
  }
};

export default Products;
